form.search {
  overflow: auto;
  margin-bottom: 10px;
  width: 300px;
}

form.search button[type="submit"] {
  float: right;
  margin-top: 10px;
}


.form-control {
  display: block;
  width: 100%;
  height: calc(1.5em + .75rem + 2px);
  padding: .375rem .75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #435970;
  border-radius: 32px;
  font-size: 12px;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.mt-4, .my-4 {
  margin-top: 1.5rem!important;
}

.lead {
  font-size: 14px;
  font-weight: 300;
}

#message{
  color:red;
}


.card {
  /* position: relative; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-color: #fff;
  background-clip: border-box;
  border: 1px solid rgba(0,0,0,.125);
  border-radius: .25rem;
  /* width: 780px;
  height: 370px; */
}

.theme-card-body {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-color: transparent;
  background-clip: border-box;
  border: 1px solid rgba(0,0,0,.125);
  border-radius: .25rem;
  width: 780px;
  height: 370px;
}

.theme {
  background: #fff;  
  height: 120px;
  margin: 0.5rem;
  margin-right: -1rem;
  /* position: relative; */
  width: 180px;
  box-shadow: 0 -3px 15px #6f6666, 0 3px 15px #6f6666;
  text-align: left;
}

.theme > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  /* background:#6CADDC; */
  cursor: pointer;
  
}
.theme > .img-container > img {
  width: 100%;
  
}
.choosetheme {
 color: whitesmoke;
 text-align: center;
 font-size: 32px;
}

.theme > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.theme > .remove:hover {
  color: #d50000;
}

.upage-box {
  background: #fff;
  border-radius: 2px;
  height: 120px;
  margin: 1rem;
